import React from 'react'
import PropTypes from 'prop-types'
import Helmet from 'react-helmet'
import favicon from '../assets/me.png';

import Header from '../components/Header/Header'

const Layout = ({ children, data }) => (
  <div>
    <Helmet
      title={data.site.siteMetadata.title}
      meta={[
        { name: `description`, content: 'Portfolio website for Thidasa Pankaja Paranavitharana. Undergraduate. Developer' },
        { name: 'keywords', content: 'thidasa, pankaja, fullstack, javascript, reactjs, nodejs, java, web developer, frontend, python' },
      ]}
    >
      <link rel="shortcut icon" href={favicon} />
    </Helmet>
    <Header siteTitle={data.site.siteMetadata.title} className='FullHeader'/>
    <div className='FullContent'>
      {children()}
    </div>
  </div>
)

Layout.propTypes = {
  children: PropTypes.func,
}

export default Layout

export const query = graphql`
  query SiteTitleQuery {
    site {
      siteMetadata {
        title
      }
    }
  }
`



